@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100');
@import url('https://fonts.googleapis.com/css?family=Jura');

html {
  display: flex;
  height: 100%;
}

body {
  background-color:  #dcdcdc;
  color: #2f4f4f;
  display: flex;
  flex-grow: 1;
  margin-left: 5%;
  margin-right: 5%;

  #root {
    display: flex;
    flex-grow: 1;
  }

  a {
    color: #0000FF;
    text-decoration: underline;
  }
}

header {
  h1, h2 {
    font-family: 'IBM Plex Sans', sans-serif;
  }
}

nav {
  font-family: 'Jura', sans-serif;
  justify-content: center !important;

  a {
     font-size: 1.5em !important;

    --underlineNav-borderColor-active: #2f4f4f;
  }
}

main {
  font-family: 'Jura', sans-serif;

  .about {
    p {
      margin: 0px;
    }
  }

  .projects {
    ul {
      list-style-type: none;

      li:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }

  .resume {
    height: 90%;

    @media screen and (max-width: 800px) {
      width: 100%;
    }

    @media screen and (min-width: 800px) and (max-width: 1200px) {
      width: 50%;
    }

    @media screen and (min-width: 1200px) {
      width: 40%;
    }

    iframe {
      width: 100%;
      height: 95%;
    }
  }
}

footer {
  bottom: 1%;
  left: 0;
  right: 0;
  margin-inline: auto;
  position: absolute;
  width: fit-content;

  a:not(:last-child) {
    margin-right: 20px;
  }

  a {
    color: black !important;
    height: 50px !important;
    width: 50px !important;

    .linkedin-icon {
      display: block;
      height: 35px;
      width: 35px;
    }

    svg {
      height: 35px;
      width: auto;
    }
  }
}